import React from 'react';
import cn from 'classnames';

import { ReactComponent as CloseIcon } from './images/close.svg';
import styles from './WidgetLayout.module.scss';

interface Props {
  title?: string;
  isOpen?: boolean;
  onClose?: () => void;
}

export const WidgetLayout: React.FC<Props> = ({ isOpen = false, title, children, onClose }) => {
  const rootClassName = cn(styles.root, {
    [styles.rootOpen]: isOpen,
  });

  return (
    <div className={rootClassName}>
      <div className={styles.container}>
        <div className={styles.content}>
          <span role="button" className={styles.closeIcon} onClick={onClose} onKeyDown={onClose} tabIndex={0}>
            <CloseIcon />
          </span>

          {title && <div className={styles.title}>{title}</div>}

          <div className={styles.body}>{children}</div>
        </div>
      </div>
    </div>
  );
};
