import React from 'react';
import cn from 'classnames';

import styles from './SharedNavigation.module.scss';

interface SubItem {
  name: string;
  url: string;
}

interface Item {
  name: string;
  url: string;
  children?: SubItem[];
}

interface Props {
  items: Item[];
}

export const SharedNavigation: React.FC<Props> = ({ items }) => {
  const [openItemIndex, setOpenItemIndex] = React.useState<number>();

  const createClickHandler = (index: number) => () => {
    if (openItemIndex === index) {
      setOpenItemIndex(-1);
    } else {
      setOpenItemIndex(index);
    }
  };

  return (
    <ul className={styles.root}>
      {items.map((item, index) =>
        item.children === undefined ? (
          <li className={styles.item} key={item.url}>
            <a className={styles.link} href={item.url}>
              {item.name}
            </a>
          </li>
        ) : (
          <li className={cn(styles.itemComplex, { [styles.itemOpen]: openItemIndex === index })} key={item.url}>
            <a className={styles.link} href={item.url}>
              {item.name}
            </a>

            <button className={styles.button} type="button" onClick={createClickHandler(index)}>
              {item.name}
            </button>

            <ul className={styles.subList}>
              {item.children.map(subItem => (
                <li className={styles.subItem} key={subItem.url}>
                  <a href={subItem.url} className={styles.subLink}>
                    {subItem.name}
                  </a>
                </li>
              ))}
            </ul>
          </li>
        ),
      )}
    </ul>
  );
};
