import React from 'react';

import { ReactComponent as CloseIcon } from './images/close.svg';
import styles from './FileList.module.scss';

export type FileListProps = {
  onRemove: (fileName: string) => void;
  fileList: File[];
};

const getFileSize = (file: File) => `${(file.size / 1024 / 1024).toFixed(2)}\u00A0`;

export const FileList: React.FC<FileListProps> = ({ fileList, onRemove }) => {
  return (
    <ul className={styles.root}>
      {fileList.map(file => (
        <li key={file.name} className={styles.file}>
          <span className={styles.fileName}>{file.name}</span>

          <div className={styles.fileControl}>
            <span className={styles.size}>{getFileSize(file)}</span>

            <span
              role="button"
              className={styles.removeIcon}
              onClick={() => onRemove(file.name)}
              onKeyPress={() => onRemove(file.name)}
              tabIndex={0}
            >
              <CloseIcon />
            </span>
          </div>
        </li>
      ))}
    </ul>
  );
};
