import { FieldRenderProps } from 'react-final-form';
import cn from 'classnames';

import styles from './FormField.module.scss';

export const getFormFieldProps = <P extends FieldRenderProps<string, HTMLElement>>(
    meta: P['meta'],
    containerClassName?: string
) => {
    const errorMessage = (meta.touched && meta.error) || meta.submitError;

    return {
        containerClassName: cn(styles.container, containerClassName),
        message: errorMessage,
        isError: !!errorMessage,
        isHighlighted: !errorMessage && !!meta.data?.isHighlighted,
    };
};
