import React from 'react';
import cn from 'classnames';

import { ReactComponent as ErrorIcon } from './icons/error.svg';
import { ReactComponent as InfoIcon } from './icons/info.svg';
import { ReactComponent as SuccessIcon } from './icons/success.svg';
import { ReactComponent as WarningIcon } from './icons/warning.svg';
import { ReactComponent as ErrorIconFilled } from './icons/error-filled.svg';
import { ReactComponent as InfoIconFilled } from './icons/info-filled.svg';
import { ReactComponent as SuccessIconFilled } from './icons/success-filled.svg';
import { ReactComponent as WarningIconFilled } from './icons/warning-filled.svg';
import styles from './Alert.module.scss';

export type Props = {
    severity?: 'error' | 'info' | 'success' | 'warning';
    variant?: 'filled' | 'outlined' | 'standard' | 'none';
    icon?: React.ElementType | null;
    className?: string;
    isIconFilled?: boolean;
};

export const Alert: React.FC<Props> = ({
    severity = 'success',
    variant = 'none',
    children = '',
    icon = null,
    isIconFilled = false,
    className,
}) => {
    const icons = {
        error: isIconFilled ? ErrorIconFilled : ErrorIcon,
        info: isIconFilled ? InfoIconFilled : InfoIcon,
        success: isIconFilled ? SuccessIconFilled : SuccessIcon,
        warning: isIconFilled ? WarningIconFilled : WarningIcon,
    };

    const IconComponent = icon || icons[severity];

    return (
        <div className={cn(styles.root, styles[severity], styles[variant], className)} role="alert">
            <IconComponent className={cn(styles.icon, variant !== 'filled' && styles[`${severity}Icon`])} />
            {!!children && <span className={styles.text}>{children}</span>}
        </div>
    );
};
