import React from 'react';

import styles from './CustomNavigation.module.scss';

interface Item {
  name: string;
  url: string;
}

interface Props {
  items: Item[];
}

export const CustomNavigation: React.FC<Props> = ({ items }) => {
  return (
    <ul className={styles.list}>
      {items.map(item => (
        <li className={styles.item} key={item.url}>
          <a className={styles.link} href={item.url}>
            {item.name}
          </a>
        </li>
      ))}
    </ul>
  );
};
