import React from 'react';
import cn from 'classnames';
import MaskedInput from 'react-text-mask';

import styles from './Input.module.scss';
import { SmoothHeightWrapper } from '../SmoothHeightWrapper';

type Value = string;

export type Props = JSX.IntrinsicElements['input'] & {
    value?: Value;
    title?: string;
    message?: string;
    mask?: (string | RegExp)[];
    className?: string;
    containerClassName?: string;
    isError?: boolean;
    isSuccess?: boolean;
    isHighlighted?: boolean;
    onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
    showMask?: boolean;
    size?: 'large' | 'medium' | 'small';
};

function isValueEmpty(value?: Value) {
    return value === undefined || value === '';
}

export const Input: React.FC<Props> = ({
    value,
    isError = false,
    isSuccess = false,
    isHighlighted = false,
    title,
    message,
    required,
    mask,
    className,
    containerClassName,
    showMask,
    size = 'large',
    ...rest
}) => {
    const rootClassName = React.useMemo(() => {
        return cn(
            styles.root,
            {
                [styles.rootStatusHighlighted]: isHighlighted,
                [styles.rootStatusError]: isError,
                [styles.rootStatusSuccess]: isSuccess,
                [styles.rootFilled]: !isValueEmpty(value),
            },
            containerClassName
        );
    }, [isHighlighted, isError, isSuccess, value, containerClassName]);
    const InputComponent = mask ? MaskedInput : 'input';

    return (
        <div className={rootClassName}>
            {!!title && (
                <div className={styles.title}>
                    <span>{title}</span>

                    {required && <span className={styles.attention}>*</span>}
                </div>
            )}

            <InputComponent
                className={cn(
                    styles.input,
                    {
                        [styles.sizeMedium]: size === 'medium',
                        [styles.sizeSmall]: size === 'small',
                    },
                    className
                )}
                mask={mask}
                required={required}
                value={value}
                showMask={showMask}
                {...rest}
            />

            <SmoothHeightWrapper>
                {Boolean(message) && <div className={styles.message}>{message}</div>}
            </SmoothHeightWrapper>
        </div>
    );
};
