import React from 'react';

import { PortalFrame } from './components/PortalFrame';
import { CustomNavigation } from './components/CustomNavigation';
import { SearchForm } from './components/SearchForm';
import { SharedNavigation } from './components/SharedNavigation';
import styles from './MainMenu.module.scss';
import { ReactComponent as CrossIcon } from './images/cross.svg';
import { customLinks, sharedLinks } from './mock';

interface Props {
  isOpen: boolean;
  onClose: () => void;
}

export const MainMenu: React.FC<Props> = ({ isOpen, onClose }) => {
  const handleSubmit = React.useCallback((value: string) => {
    window.location.href = `https://www.utair.ru/search/?q=${value}`;
  }, []);

  return (
    <PortalFrame isOpen={isOpen}>
      <div className={styles.root}>
        <div className={styles.customNavigation}>
          <CustomNavigation items={customLinks} />
        </div>

        <div className={styles.searchForm}>
          <SearchForm onSubmit={handleSubmit} />
        </div>

        <div className={styles.sharedNavigation}>
          <SharedNavigation items={sharedLinks} />
        </div>

        <button className={styles.closeButton} onClick={onClose}>
          <CrossIcon className={styles.crossIcon} />
        </button>
      </div>
    </PortalFrame>
  );
};
