import React from 'react';
import cn from 'classnames';

import { Loader } from '../Loader';
import { Portal } from '../Portal';
import styles from './LoaderFullscreen.module.scss';

interface Props {
  opaque?: boolean;
}

export const LoaderFullscreen: React.FC<Props> = ({ opaque }) => {
  return (
    <Portal>
      <div className={cn(styles.root, opaque && styles.rootOpaque)}>
        <Loader />
      </div>
    </Portal>
  );
};
