import React from 'react';
import cn from 'classnames';

import styles from './TextArea.module.scss';

type Value = string;

export type Props = JSX.IntrinsicElements['textarea'] & {
    value?: Value;
    title?: string;
    message?: string;
    inputClassName?: string;
    isError?: boolean;
    isSuccess?: boolean;
    onChange?: (event: React.ChangeEvent<HTMLTextAreaElement>) => void;
};

function isValueEmpty(value?: Value) {
    return value === undefined || value === '';
}

export const TextArea: React.FC<Props> = ({
    value,
    isError = false,
    isSuccess = false,
    title,
    message,
    required,
    placeholder,
    className,
    onChange,
}) => {
    const rootClassName = React.useMemo(() => {
        return cn(styles.root, {
            [styles.rootStatusError]: isError,
            [styles.rootStatusSuccess]: isSuccess,
            [styles.rootFilled]: !isValueEmpty(value),
        });
    }, [isError, isSuccess, value]);

    return (
        <div className={rootClassName}>
            <div className={styles.title}>
                <span>{title}</span>

                {required && <span className={styles.attention}>*</span>}
            </div>

            <textarea
                className={cn(styles.input, styles.inputTextArea, className)}
                value={value}
                placeholder={placeholder}
                required={required}
                onChange={onChange}
            />

            {message !== undefined && <div className={styles.message}>{message}</div>}
        </div>
    );
};
