import React from 'react';

import { ReactComponent as LensIcon } from './images/lens.svg';
import styles from './SearchForm.module.scss';

interface Props {
  onSubmit?: (value: string) => void;
}

export const SearchForm: React.FC<Props> = ({ onSubmit }) => {
  const [value, setValue] = React.useState('');

  const handleChange = React.useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    setValue(event.target.value);
  }, []);

  const handleSubmit = React.useCallback(
    (event: React.FormEvent) => {
      event.preventDefault();

      if (onSubmit !== undefined) {
        onSubmit(value.trim());
      }
    },
    [value, onSubmit],
  );

  return (
    <form className={styles.root} onSubmit={handleSubmit}>
      <input className={styles.input} type="text" placeholder="" value={value} onChange={handleChange} />

      <button type="submit" className={styles.button}>
        <LensIcon className={styles.icon} />
      </button>
    </form>
  );
};
