import { useState, useEffect } from 'react';

export default function useCountDown(seconds = 60): [number, () => void] {
    const [timeLeft, setTimeLeft] = useState(seconds);
    const [start, setStart] = useState(new Date());

    useEffect(() => {
        const timer = setInterval(() => {
            const remain = Math.floor(seconds + 1 - (Date.now() - start.getTime()) / 1000);

            setTimeLeft(remain);

            if (remain <= 0) {
                clearInterval(timer);
            }
        }, 1000);

        return () => clearInterval(timer);
    }, [start, seconds]);

    return [
        timeLeft,
        () => {
            setStart(new Date());
            setTimeLeft(seconds);
        },
    ];
}
