import React from 'react';
import cn from 'classnames';
import { CSSTransition } from 'react-transition-group';

import styles from './CollapseTransition.module.scss';

interface Props {
  opened: boolean;
  unmountOnExit?: boolean;
}

const animationTimeout = 300;

export const CollapseTransition: React.FC<Props> = ({ opened, children, unmountOnExit = true }) => {
  const rootRef = React.useRef<HTMLDivElement>(null);
  const wrapperRef = React.useRef<HTMLDivElement>(null);

  React.useLayoutEffect(() => {
    if (rootRef.current) {
      rootRef.current.style.height = opened ? 'auto' : '0';
    }
  }, [opened]);

  function handleEntering(el: HTMLElement) {
    const offset = wrapperRef.current ? wrapperRef.current.clientHeight : 0;

    // eslint-disable-next-line no-param-reassign
    el.style.height = '0';

    // eslint-disable-next-line no-void
    void el.offsetHeight; // force reflow

    // eslint-disable-next-line no-undef
    requestAnimationFrame(() => {
      // eslint-disable-next-line no-param-reassign
      el.style.height = `${offset}px`;
    });
  }

  function handleExiting(el: HTMLElement) {
    // eslint-disable-next-line no-param-reassign
    el.style.height = `${el.offsetHeight}px`;

    // eslint-disable-next-line no-void
    void el.offsetHeight; // force reflow

    // eslint-disable-next-line no-undef
    requestAnimationFrame(() => {
      // eslint-disable-next-line no-param-reassign
      el.style.height = '0';
    });
  }

  function handleEntered(el: HTMLElement) {
    // eslint-disable-next-line no-param-reassign
    el.style.height = 'auto';
  }

  function handleExited(el: HTMLElement) {
    // eslint-disable-next-line no-param-reassign
    el.style.height = '0';
  }

  return (
    <div>
      <CSSTransition
        in={opened}
        unmountOnExit={unmountOnExit}
        timeout={animationTimeout}
        onEntering={handleEntering}
        onEntered={handleEntered}
        onExiting={handleExiting}
        onExited={handleExited}
        classNames={{
          appear: '',
          appearActive: '',
          appearDone: '',
          enter: '',
          enterActive: styles.rootEnterActive,
          enterDone: '',
          exit: '',
          exitActive: styles.rootExitActive,
          exitDone: '',
        }}
      >
        {() => (
          <div className={cn({ [styles.rootClosed]: !opened })} ref={rootRef}>
            <div ref={wrapperRef}>{children}</div>
          </div>
        )}
      </CSSTransition>
    </div>
  );
};
