import React from 'react';
import cn from 'classnames';

import styles from './Button.module.scss';

type Icon = JSX.Element;

export type Props = JSX.IntrinsicElements['button'] & {
    variant?: 'primary' | 'secondary' | 'outlined' | 'text';
    size?: 'large' | 'medium' | 'small';
    fullWidth?: boolean;
    startIcon?: Icon;
    endIcon?: Icon;
};

export const Button: React.FC<Props> = ({
    children,
    className,
    type = 'button',
    variant = 'primary',
    size = 'large',
    fullWidth = false,
    startIcon,
    endIcon,
    ...restProps
}) => {
    const rootClassName = cn(styles.root, className, {
        [styles.rootVariantPrimary]: variant === 'primary',
        [styles.rootVariantSecondary]: variant === 'secondary',
        [styles.rootVariantOutlined]: variant === 'outlined',
        [styles.rootVariantText]: variant === 'text',
        [styles.sizeLarge]: size === 'large',
        [styles.sizeMedium]: size === 'medium',
        [styles.sizeSmall]: size === 'small',
        [styles.fullWidth]: fullWidth,
    });

    return (
        <button type={type} className={rootClassName} {...restProps}>
            {!!startIcon && <span className={cn(styles.icon, styles.startIcon)}>{startIcon}</span>}
            {children}
            {!!endIcon && <span className={cn(styles.icon, styles.endIcon)}>{endIcon}</span>}
        </button>
    );
};
