import React from 'react';
import { CSSTransition } from 'react-transition-group';

import { Portal } from '../../../Portal';
import styles from './PortalFrame.module.scss';

interface Props {
  isOpen: boolean;
}

function disableScroll() {
  document.body.style.overflow = 'hidden';
}

function enableScroll() {
  document.body.style.overflow = '';
}

const transitionClassNames = {
  appear: styles.rootEnter,
  appearActive: styles.rootEnterActive,
  exit: styles.rootExit,
  exitActive: styles.rootExitActive,
};

export const PortalFrame: React.FC<Props> = ({ isOpen = false, children }) => {
  const [keepMounted, setKeepMounted] = React.useState(isOpen);

  const handleEnter = React.useCallback(() => {
    disableScroll();
    setKeepMounted(true);
  }, []);

  const handleExited = React.useCallback(() => {
    enableScroll();
    setKeepMounted(false);
  }, []);

  if (!isOpen && !keepMounted) {
    return null;
  }

  return (
    <Portal>
      <CSSTransition
        appear
        unmountOnExit
        in={isOpen}
        timeout={Number(styles.timeout)}
        onEnter={handleEnter}
        onExited={handleExited}
        classNames={transitionClassNames}
      >
        <div className={styles.root}>
          <div className={styles.container}>{children}</div>
        </div>
      </CSSTransition>
    </Portal>
  );
};
