import React from 'react';
import cn from 'classnames';

import styles from './Loader.module.scss';

interface Props {
  className?: string;
  light?: boolean;
  small?: boolean;
}

export const Loader: React.FC<Props> = ({ className, small, light }) => {
  const svgBodyRef = React.useRef<SVGPathElement>(null);
  const isIE = 'documentMode' in document;
  const rootClassName = cn(
    styles.root,
    small && styles.rootSmall,
    light && styles.rootLight,
    isIE && styles.rootIE,
    className,
  );

  React.useLayoutEffect(() => {
    function run(offset: number) {
      if (svgBodyRef.current) {
        svgBodyRef.current.style.strokeDashoffset = String(offset);
        requestAnimationFrame(run.bind(null, offset - 1));
      }
    }

    if (isIE) {
      run(0);
    }
  }, [isIE]);

  return (
    <svg className={rootClassName} viewBox="0 0 41 37">
      <path
        ref={svgBodyRef}
        d="M13.3-2.6L5 16.9c-1.2 3-1.5 5.8-1.3 8.3.2 1.6.9 3.7 2.4 5.4
                        1.7 1.8 4.4 3.1 7.2 3.1 5.6 0 10.8-2.8 13.9-10.1L38.1-1.7"
        fill="none"
        stroke="currentColor"
        strokeWidth="6.7"
        strokeMiterlimit="10"
      />
    </svg>
  );
};
