import React from 'react';

import { UploadButton, UploadButtonProps } from './UploadButton';
import { FileList, FileListProps } from './FileList';
import styles from './Attachment.module.scss';

type Props = UploadButtonProps & FileListProps;

export const Attachment: React.FC<Props> = ({ onRemove, fileList, ...fileInputProps }) => (
  <div>
    <div className={styles.fileListContainer}>
      <FileList fileList={fileList} onRemove={onRemove} />
    </div>

    <div className={styles.uploadContainer}>
      <UploadButton {...fileInputProps} attached={fileList.length > 0} />
    </div>
  </div>
);
