import React from 'react';
import cn from 'classnames';

import styles from './Checkbox.module.scss';
import { Check, Props as CheckProps } from './Check';

export type Props = CheckProps & {
    containerClassName?: string;
    labelClassName?: string;
    label: React.ReactNode;
    name: string;
};

export const Checkbox: React.FC<Props> = ({
    className,
    containerClassName,
    labelClassName,
    label,
    variant = 'outlined',
    name,
    checked,
    readOnly = false,
    isError = false,
    onChange,
    ...rest
}) => {
    return (
        <label className={cn(styles.root, containerClassName)} htmlFor={name}>
            <Check {...{ className, variant, name, checked, readOnly, isError, onChange }} {...rest} />

            <span className={cn(styles.label, labelClassName)}>{label}</span>
        </label>
    );
};
