import React, { useRef, useCallback, useLayoutEffect, useEffect } from 'react';

import styles from './SmoothHeightWrapper.module.scss';

export const SmoothHeightWrapper: React.FC = ({ children }) => {
    const rootRef = useRef<HTMLDivElement>(null);
    const wrapperRef = useRef<HTMLDivElement>(null);
    const heightRef = useRef(0);
    const applyChanges = useCallback(() => {
        if (rootRef.current && wrapperRef.current) {
            const height = wrapperRef.current.offsetHeight;

            if (heightRef.current !== height) {
                heightRef.current = height;

                rootRef.current.style.height = `${height}px`;
            }
        }
    }, []);

    useLayoutEffect(applyChanges, [applyChanges, children]);

    useEffect(() => {
        window.addEventListener('resize', applyChanges);

        return () => window.removeEventListener('resize', applyChanges);
    }, [applyChanges]);

    return (
        <div ref={rootRef} className={styles.root}>
            <div ref={wrapperRef}>{children}</div>
        </div>
    );
};
