import React, { useCallback, useContext, useState, useEffect } from 'react';
import ReactDOM from 'react-dom';
import cn from 'classnames';

import { ReactComponent as Close } from './images/close.svg';
import styles from './Modal.module.scss';
import { Portal } from '../Portal';
import { Loader } from '../Loader';
import useDisableScroll from './useDisableScroll';

type Size = 'small' | 'medium' | 'large';

type ClassName =
    | 'modal'
    | 'modalStickyUp'
    | 'container'
    | 'body'
    | 'close'
    | 'closeIcon'
    | 'loader'
    | 'loaderOverlay'
    | 'image'
    | 'content'
    | 'title'
    | 'description'
    | 'buttons'
    | 'addon'
    | 'footer'
    | 'bodyScroll'
    | 'paperScroll'
    | 'blueBackground'
    | 'whiteBackground'
    | Size;

interface ClassesInterface {
    [key: string]: string;
}

type Classes = ClassesInterface | undefined | false;

export type Props = JSX.IntrinsicElements['div'] & {
    className: string;
    prefix?: string;
    size?: Size;
    bodyTestId?: string;
    closeButtonTestId?: string;
    disableOverlayClick?: boolean;
    isLoading?: boolean;
    classes?: Classes;
    scroll?: 'body' | 'paper';
    background?: 'blue' | 'white';
    onClose: { (): void };
};

type ItemProps = JSX.IntrinsicElements['div'] & {
    centered?: boolean;
};

type Item = React.FC<ItemProps>;

type ButtonsProps = ItemProps & {
    row?: boolean;
};

type Buttons = React.FC<ButtonsProps>;

type BaseProps = ItemProps & {
    target: ClassName;
};

type Base = React.FC<BaseProps>;

type Modal = React.FC<Props> & {
    Image: Item;
    Title: Item;
    Description: Item;
    Content: Item;
    Buttons: Buttons;
    Addon: Item;
    /**
     * @deprecated The prop should not be used
     */
    Footer: Item;
};

type Container = HTMLDivElement | null | undefined;

const FooterContainer = React.createContext<Container>(undefined);
const ComputeClassName = React.createContext<Function>(() => undefined);

function getComputedClassName(name: ClassName, condition = true, classes: Classes = {}, prefix?: string) {
    if (!condition) return undefined;

    const prefixed = prefix && `${prefix}-${name}`;
    const className = classes && classes[name];

    return cn(prefixed, styles[name], className);
}

export const Modal: Modal = ({
    prefix,
    className,
    classes,
    size = 'medium',
    bodyTestId,
    closeButtonTestId,
    disableOverlayClick,
    isLoading,
    children,
    scroll = 'body',
    background = 'blue',
    onClose,
}) => {
    const [container, setContainer] = useState<Container>();
    const ccn = useCallback(
        (name: ClassName, condition = true) => getComputedClassName(name, condition, classes, prefix),
        [classes, prefix]
    );

    const [isOpen, setIsOpen] = useState<boolean>(false);

    useEffect(() => {
        if (container) setIsOpen(true);
    }, [container]);

    const close = () => {
        setIsOpen(false);
        setTimeout(onClose, 300);
    };

    useDisableScroll(true);

    return (
        <Portal>
            <FooterContainer.Provider value={container}>
                <ComputeClassName.Provider value={ccn}>
                    <div
                        className={cn(
                            ccn('modal'),
                            isOpen && ccn('modalStickyUp'),
                            ccn(`${scroll}Scroll` as ClassName),
                            ccn(`${background}Background` as ClassName),
                            className
                        )}
                        onClick={disableOverlayClick ? undefined : (e) => e.currentTarget === e.target && close()}
                        role="presentation"
                    >
                        <div ref={setContainer} className={cn(ccn('container'), ccn(size))}>
                            <div className={ccn('body')} data-testid={bodyTestId}>
                                <button className={ccn('close')} data-testid={closeButtonTestId} onClick={close}>
                                    <Close className={ccn('closeIcon')} />
                                </button>
                                {children}
                                {isLoading && (
                                    <div className={ccn('loaderOverlay')}>
                                        <Loader small className={ccn('loader')} />
                                    </div>
                                )}
                            </div>
                        </div>
                    </div>
                </ComputeClassName.Provider>
            </FooterContainer.Provider>
        </Portal>
    );
};

const ModalBase: Base = ({ className, target, centered = false, ...rest }) => {
    const ccn = useContext(ComputeClassName);

    return <div className={cn(ccn(target), ccn('centered', centered), className)} {...rest} />;
};

const ModalImage: Item = (props) => <ModalBase target="image" {...props} />;

const ModalTitle: Item = (props) => <ModalBase target="title" {...props} />;

const ModalDescription: Item = (props) => <ModalBase target="description" {...props} />;

const ModalContent: Item = (props) => <ModalBase target="content" {...props} />;

const ModalAddon: Item = (props) => <ModalBase target="addon" {...props} />;

const ModalButtons: Buttons = ({ className, centered = false, row = false, ...rest }) => {
    const ccn = useContext(ComputeClassName);

    return <div className={cn(ccn('buttons'), ccn('row', row), ccn('centered', centered), className)} {...rest} />;
};

const ModalFooter: Item = (props) => {
    const container = useContext(FooterContainer);

    if (!container) return null;

    return ReactDOM.createPortal(<ModalBase target="footer" {...props} />, container);
};

Modal.Image = ModalImage;
Modal.Title = ModalTitle;
Modal.Description = ModalDescription;
Modal.Content = ModalContent;
Modal.Addon = ModalAddon;
Modal.Buttons = ModalButtons;
Modal.Footer = ModalFooter;
