import React from 'react';
import cn from 'classnames';

import { ReactComponent as CheckboxIcon } from './images/icon-checkbox.svg';
import { ReactComponent as CheckboxOutlinedIcon } from './images/icon-checkbox-outlined.svg';
import { ReactComponent as CheckboxContainedIcon } from './images/icon-checkbox-contained.svg';
import styles from './Check.module.scss';

export type Props = JSX.IntrinsicElements['input'] & {
    variant?: 'outlined' | 'contained';
    isError?: boolean;
};

export const Check: React.FC<Props> = ({
    className,
    variant = 'outlined',
    name,
    checked,
    readOnly = false,
    isError = false,
    onChange,
    ...rest
}) => {
    const CheckedIcon = variant === 'contained' ? CheckboxContainedIcon : CheckboxOutlinedIcon;
    const Icon = checked ? CheckedIcon : CheckboxIcon;

    return (
        <span className={cn(styles.root, checked && styles.checked, isError && styles.error, className)}>
            <Icon focusable="false" role="presentation" aria-hidden="true" className={cn(styles.icon)} />

            <input
                type="checkbox"
                id={name}
                className={styles.input}
                name={name}
                value={String(!checked)}
                checked={checked}
                readOnly={readOnly}
                onChange={readOnly ? undefined : onChange}
                {...rest}
            />
        </span>
    );
};
