import React from 'react';
import cn from 'classnames';

import { RadioGroup, RadioGroupProps } from './RadioGroup';
import { Radio } from './Radio';
import styles from './RadioButton.module.scss';

interface Option {
    value: string;
    label: string;
}

export interface Props extends RadioGroupProps {
    options: Option[];
    className?: string;
    title?: string;
    required?: boolean;
    message?: string;
    isError?: boolean;
    isSuccess?: boolean;
    onChange?: (value: React.ChangeEvent<HTMLInputElement>) => void;
}

export const RadioButton: React.FC<Props> = ({
    options,
    className,
    name,
    value,
    onChange,
    title,
    required,
    message,
    isError,
    isSuccess,
}) => {
    const rootClassName = cn(styles.root, {
        [styles.rootStatusError]: isError,
        [styles.rootStatusSuccess]: isSuccess,
    });

    return (
        <div className={cn(rootClassName, className)}>
            <div className={styles.title}>
                <span>{title}</span>

                {required && <span className={styles.attention}>*</span>}
            </div>

            <RadioGroup name={name} value={value} onChange={onChange}>
                {options.map((option) => (
                    <label key={option.value} htmlFor={option.value} className={styles.controlLabel}>
                        <Radio id={option.value} value={option.value} className={cn(styles.icon, isError && styles.iconError)} />

                        <span className={styles.controlLabelText}>{option.label}</span>
                    </label>
                ))}
            </RadioGroup>

            {message !== undefined && <div className={styles.message}>{message}</div>}
        </div>
    );
};
