import { Input } from '../Input';
import { FormStringField } from '../../types';
import { Props as InputProps } from '../Input/Input';
import { getFormFieldProps } from '../../helpers';

export type Props = InputProps & FormStringField;

export const FormInput: Props = ({ input, meta, containerClassName, ...rest }) =>
    Input({
        ...input,
        ...getFormFieldProps(meta, containerClassName),
        ...rest,
    });
