import cn from 'classnames';
import React from 'react';

import styles from './NativeSelect.module.scss';
import { SmoothHeightWrapper } from '../SmoothHeightWrapper';

type Option = {
    value: string;
    label: string;
};

export type Props = JSX.IntrinsicElements['select'] & {
    value?: string;
    options: Option[];
    title?: string;
    className?: string;
    containerClassName?: string;
    isHighlighted?: boolean;
    isError?: boolean;
    isSuccess?: boolean;
    onChange: (value: string) => void;
    onFocus?: () => void;
    message?: string;
    showEmptyOption?: boolean;
};

function isValueEmpty(value?: string) {
    return value === undefined || value === '';
}

export const NativeSelect: React.FC<Props> = ({
    value,
    title,
    className,
    containerClassName,
    isHighlighted,
    isError,
    isSuccess,
    onChange,
    onFocus,
    required,
    message,
    options,
    placeholder,
    showEmptyOption,
}) => {
    const rootClassName = cn(
        styles.root,
        {
            [styles.rootStatusHighlighted]: isHighlighted,
            [styles.rootStatusError]: isError,
            [styles.rootStatusSuccess]: isSuccess,
            [styles.rootFilled]: !isValueEmpty(value),
        },
        containerClassName
    );

    const handleChange = (event: React.ChangeEvent<HTMLSelectElement>) => {
        const { value: currentValue } = event.target;

        onChange(currentValue);
    };

    return (
        <div className={rootClassName}>
            <div className={styles.title}>
                <span>{title}</span>

                {required && <span className={styles.attention}>*</span>}
            </div>

            <div className={styles.selectWrapper}>
                <select
                    onFocus={onFocus}
                    className={cn(styles.select, className)}
                    onChange={handleChange}
                    value={value}
                >
                    {showEmptyOption && <option value="">{placeholder}</option>}

                    {options.map((item) => (
                        <option key={item.value} value={item.value}>
                            {item.label}
                        </option>
                    ))}
                </select>
            </div>

            <SmoothHeightWrapper>
                {Boolean(message) && <div className={styles.message}>{message}</div>}
            </SmoothHeightWrapper>
        </div>
    );
};
