import React, { useState } from 'react';
import cn from 'classnames';

import { Input } from '../Input';
import useCountDown from './useCountDown';
import styles from './ConfirmCode.module.scss';

function formatTimeLeft(timeLeft: number): string {
    const minutes: number = Math.floor(timeLeft / 60);
    const seconds: number = timeLeft % 60;
    const normalize = (num: number): string => `0${num}`.slice(-2);

    return `${normalize(minutes)}:${normalize(seconds)}`;
}

function setCaretPosition(elem: HTMLInputElement, pos: number) {
    elem.setSelectionRange(pos, pos);
}

function extractValue(value: string): string {
    return value.replace(/\D/g, '');
}

export type RenderTimeLeft = {
    (
        label: string,
        timeLeft: number,
        formatTimer: typeof formatTimeLeft,
        textClassName: string,
        timerClassName: string
    ): JSX.Element;
};

const defaultRenderTimeLeft: RenderTimeLeft = (label, timeLeft, formatTimer, textClassName, timerClassName) => (
    <div className={textClassName}>
        {label} <span className={timerClassName}>{formatTimer(timeLeft)}</span>
    </div>
);

export type RenderRepeat = {
    (
        label: string,
        onClick: React.MouseEventHandler<HTMLElement>,
        buttonClassName: string,
        textClassName: string
    ): JSX.Element;
};

const defaultRenderRepeat: RenderRepeat = (label, onClick, buttonClassName) => (
    <button className={buttonClassName} onClick={onClick}>
        {label}
    </button>
);

export type Props = {
    className?: string;
    error?: string;
    onInput: (value: string) => void;
    onRequestCode: () => void;
    length?: number;
    time?: number;
    timeLeftLabel?: string;
    repeatButtonLabel?: string;
    renderTimeLeft?: RenderTimeLeft;
    renderRepeat?: RenderRepeat;
};

export const ConfirmCode: React.FC<Props> = ({
    className = '',
    error = '',
    onInput,
    onRequestCode,
    length = 4,
    time = 60,
    timeLeftLabel = '  :',
    repeatButtonLabel = ' ',
    renderTimeLeft = defaultRenderTimeLeft,
    renderRepeat = defaultRenderRepeat,
    ...rest
}: Props) => {
    const [code, setCode] = useState('');
    const [timeLeft, resetTimer] = useCountDown(time);
    const [isCaretHidden, setIsCaretHidden] = useState(true);

    function onChangeCode(event: React.ChangeEvent<HTMLInputElement>) {
        const value = extractValue(event.target.value);

        if (value.length === length && value !== code) {
            onInput(value);
        }

        setCode(value);
    }

    function reset() {
        onRequestCode();
        resetTimer();
        setCode('');
    }

    return (
        <div className={cn(styles.root, className)}>
            <Input
                className={cn(styles.input, isCaretHidden && styles.hiddenCaret)}
                containerClassName={styles.inputContainer}
                type="text"
                placeholder={'_'.repeat(length)}
                mask={Array(length).fill(/\d/)}
                onChange={onChangeCode}
                onFocus={(event) => {
                    const elem = event.currentTarget;
                    const position = extractValue(elem.value).length;

                    setTimeout(() => {
                        setCaretPosition(elem, position);
                        setIsCaretHidden(false);
                    });
                }}
                onBlur={() => setTimeout(() => setIsCaretHidden(true))}
                value={code}
                isError={!!error}
                message={error}
                showMask
                {...rest}
            />

            <div>
                {timeLeft > 0
                    ? renderTimeLeft(timeLeftLabel, timeLeft, formatTimeLeft, styles.text, styles.timer)
                    : renderRepeat(repeatButtonLabel, reset, styles.button, styles.text)}
            </div>
        </div>
    );
};
