import React, { RefObject } from 'react';
import cn from 'classnames';

import styles from './AppTooltip.module.scss';

const PLACEMENT_TOP = 'top';
const PLACEMENT_RIGHT = 'right';
const PLACEMENT_BOTTOM = 'bottom';
const PLACEMENT_LEFT = 'left';

const OPPOSITE_PLACEMENTS = {
  [PLACEMENT_TOP]: PLACEMENT_BOTTOM,
  [PLACEMENT_RIGHT]: PLACEMENT_LEFT,
  [PLACEMENT_BOTTOM]: PLACEMENT_TOP,
  [PLACEMENT_LEFT]: PLACEMENT_RIGHT,
};

export type Props = {
  placement: 'top' | 'right' | 'bottom' | 'left';
  target: RefObject<HTMLDivElement | HTMLButtonElement | null>;
};

type State = {
  autoPlacement: string;
  tooltipTop: number;
  tooltipLeft: number;
  caretTop: number;
  caretLeft: number;
};

export class AppTooltip extends React.Component<Props, State> {
  elRef = React.createRef<HTMLDivElement>();

  caretRef = React.createRef<HTMLSpanElement>();

  state = {
    // eslint-disable-next-line react/destructuring-assignment
    autoPlacement: this.props.placement,
    tooltipTop: 0,
    tooltipLeft: 0,
    caretTop: 0,
    caretLeft: 0,
  };

  componentDidMount() {
    const { target } = this.props;

    if (typeof window !== 'undefined' && target.current) {
      window.addEventListener('resize', this.calculatePlacement);
      window.addEventListener('scroll', this.calculatePlacement);
    }

    if (target.current) {
      this.calculatePlacement();
    }
  }

  componentWillUnmount() {
    const { target } = this.props;

    if (target.current && typeof window !== 'undefined') {
      window.removeEventListener('resize', this.calculatePlacement);
      window.removeEventListener('scroll', this.calculatePlacement);
    }
  }

  getAutoPlacement(): string {
    const { placement, target } = this.props;
    const oppositePlacement: string = OPPOSITE_PLACEMENTS[placement];

    const { clientWidth: screenWidth, clientHeight: screenHeight } = document.documentElement;

    const { top: targetTop, right: targetRight, bottom: targetBottom, left: targetLeft } = target.current
      ? target.current.getBoundingClientRect()
      : { top: 0, right: 0, bottom: 0, left: 0 };

    const { width: tooltipWidth, height: tooltipHeight } = this.elRef.current
      ? this.elRef.current.getBoundingClientRect()
      : { width: 0, height: 0 };

    const sizes = {
      top: targetTop,
      right: screenWidth - targetRight,
      bottom: screenHeight - targetBottom,
      left: targetLeft,
    };

    const tooltipSize = [PLACEMENT_TOP, PLACEMENT_BOTTOM].includes(placement) ? tooltipHeight : tooltipWidth;
    const placementSize = sizes[placement];
    // eslint-disable-next-line @typescript-eslint/ban-ts-ignore
    // @ts-ignore
    const oppositePlacementSize = sizes[oppositePlacement];

    if (tooltipSize > placementSize && oppositePlacementSize > placementSize) {
      return oppositePlacement;
    }

    return placement;
  }

  getTooltipPosition(placement: string) {
    const { target } = this.props;
    const position = {
      top: 0,
      left: 0,
    };

    const { width: tooltipWidth, height: tooltipHeight } = this.elRef.current
      ? this.elRef.current.getBoundingClientRect()
      : { width: 0, height: 0 };

    const { width: caretWidth, height: caretHeight } = this.caretRef.current
      ? this.caretRef.current.getBoundingClientRect()
      : { width: 0, height: 0 };

    const {
      top: targetTop,
      right: targetRight,
      bottom: targetBottom,
      left: targetLeft,
      width: targetWidth,
      height: targetHeight,
    } = target.current
      ? target.current.getBoundingClientRect()
      : { top: 0, right: 0, bottom: 0, left: 0, width: 0, height: 0 };

    const { clientWidth: screenWidth, clientHeight: screenHeight } = document.documentElement;

    switch (placement) {
      case PLACEMENT_TOP:
        position.top = targetTop - tooltipHeight - caretHeight;
        break;
      case PLACEMENT_BOTTOM:
        position.top = targetBottom + caretHeight;
        break;

      default: {
        const maxTop = screenHeight - tooltipHeight;
        position.top = Math.min(maxTop, Math.max(0, targetTop + targetHeight / 2 - tooltipHeight / 2));
      }
    }

    switch (placement) {
      case PLACEMENT_LEFT:
        position.left = targetLeft - tooltipWidth - caretWidth;
        break;
      case PLACEMENT_RIGHT:
        position.left = targetRight + caretWidth;
        break;

      default: {
        const maxLeft = screenWidth - tooltipWidth;
        position.left = Math.min(maxLeft, Math.max(0, targetLeft + targetWidth / 2 - tooltipWidth / 2));
      }
    }

    return position;
  }

  getCaretPosition(placement: string, tooltipPosition: { top: number; left: number }) {
    const { target } = this.props;
    const position = {
      top: 0,
      left: 0,
    };

    const { width: tooltipWidth, height: tooltipHeight } = this.elRef.current
      ? this.elRef.current.getBoundingClientRect()
      : { width: 0, height: 0 };

    const { width: caretWidth, height: caretHeight } = this.caretRef.current
      ? this.caretRef.current.getBoundingClientRect()
      : { width: 0, height: 0 };

    const { top: targetTop, right: targetRight, bottom: targetBottom, left: targetLeft } = target.current
      ? target.current.getBoundingClientRect()
      : { top: 0, right: 0, bottom: 0, left: 0 };

    const { clientWidth: screenWidth, clientHeight: screenHeight } = document.documentElement;

    switch (placement) {
      case PLACEMENT_TOP:
        position.top = tooltipHeight;
        break;
      case PLACEMENT_BOTTOM:
        position.top = caretHeight * -1;
        break;

      default: {
        const maxCaretTop = tooltipHeight - caretHeight;
        const targetVisibleHeight = Math.min(targetBottom, screenHeight) - targetTop;
        const targetVisibleCenterLeft = targetTop + targetVisibleHeight / 2 - tooltipPosition.top;
        position.top = Math.min(maxCaretTop, Math.max(0, targetVisibleCenterLeft - caretHeight / 2));
      }
    }

    switch (placement) {
      case PLACEMENT_LEFT:
        position.left = tooltipWidth;
        break;
      case PLACEMENT_RIGHT:
        position.left = caretWidth * -1;
        break;

      default: {
        const maxCaretLeft = tooltipWidth - caretWidth;
        const targetVisibleWidth = Math.min(targetRight, screenWidth) - targetLeft;
        const targetVisibleCenterLeft = targetLeft + targetVisibleWidth / 2 - tooltipPosition.left;
        position.left = Math.min(maxCaretLeft, Math.max(0, targetVisibleCenterLeft - caretWidth / 2));
      }
    }

    return position;
  }

  getStyle() {
    const { tooltipTop, tooltipLeft } = this.state;

    return { top: tooltipTop, left: tooltipLeft };
  }

  getCaretStyle() {
    const { caretTop, caretLeft } = this.state;

    return { top: caretTop, left: caretLeft };
  }

  calculatePlacement = () => {
    const placement = this.getAutoPlacement();
    const tooltipPosition = this.getTooltipPosition(placement);
    const caretPosition = this.getCaretPosition(placement, tooltipPosition);
    this.setState({
      autoPlacement: placement,
      tooltipTop: tooltipPosition.top,
      tooltipLeft: tooltipPosition.left,
      caretTop: caretPosition.top,
      caretLeft: caretPosition.left,
    });
  };

  render() {
    const { children } = this.props;
    const { autoPlacement } = this.state;

    return (
      <div ref={this.elRef} className={cn(styles.appTooltip, styles[autoPlacement])} style={this.getStyle()}>
        <div className={styles.content}>{children}</div>
        <span ref={this.caretRef} className={styles.caret} style={this.getCaretStyle()} />
      </div>
    );
  }
}
