import React from 'react';
import cn from 'classnames';

import { Button } from '../Button';
import styles from './UploadButton.module.scss';
import { ReactComponent as ClipIcon } from './images/clip.svg';

export type UploadButtonProps = JSX.IntrinsicElements['input'] & {
  buttonText: string;
  generalInfo?: string;
  message?: string;
  isSuccess?: boolean;
  isError?: boolean;
  attached?: boolean;
  isSizeOverflow?: boolean;
};

export const UploadButton: React.FC<UploadButtonProps> = ({
  title,
  required,
  buttonText,
  generalInfo,
  message,
  isSuccess,
  isError,
  isSizeOverflow,
  attached,
  ...restInputProps
}) => {
  const rootClassName = cn(styles.root, {
    [styles.rootStatusSuccess]: isSuccess,
    [styles.rootStatusError]: isError,
    [styles.rootAttached]: attached,
  });

  return (
    <div className={rootClassName}>
      <div className={styles.title}>
        <span>{title}</span>

        {required && <span className={styles.attention}>*</span>}
      </div>

      <div className={styles.inputWrapper}>
        <label className={styles.label}>
          <input className={styles.input} type="file" {...restInputProps} />

          <Button variant="outlined" className={cn(styles.button, isSizeOverflow && styles.buttonOverflowedSize)}>
            <ClipIcon className={styles.clipIcon} />

            {buttonText}
          </Button>
        </label>

        {generalInfo && <div className={styles.generalInfo}>{generalInfo}</div>}
      </div>

      {message && <div className={styles.message}>{message}</div>}
    </div>
  );
};
