import React from 'react';
import cn from 'classnames';

import { RadioGroupContext } from './RadioGroup';
import { ReactComponent as RadioIcon } from './images/icon-radio.svg';
import { ReactComponent as RadioCheckedIcon } from './images/icon-radio-checked.svg';
import styles from './Radio.module.scss';

export interface Props {
    id?: any;
    name?: string;
    checked?: boolean;
    value?: any;
    className?: string;
    onChange?: (value: React.ChangeEvent<HTMLInputElement>) => void;
    disabled?: boolean;
}

export const Radio: React.FC<Props> = ({ id, name, value, checked, className, onChange, disabled = false }) => {
    const radioContext = React.useContext(RadioGroupContext);
    let checkedProp = checked;
    let nameProp = name;
    let onChangeProp = onChange;

    if (radioContext) {
        if (typeof checked === 'undefined') {
            checkedProp = radioContext.value === value;
        }

        if (typeof name === 'undefined') {
            nameProp = radioContext.name;
        }

        if (typeof onChange === 'undefined') {
            onChangeProp = radioContext.handleChange;
        }
    }

    return (
        <span className={cn(styles.root, { [styles.checked]: checkedProp, [styles.disabled]: disabled }, className)}>
            <input
                type="radio"
                id={id}
                className={styles.input}
                name={nameProp}
                value={value}
                checked={checkedProp}
                onChange={onChangeProp}
                disabled={disabled}
            />
            <RadioIcon className={styles.icon} focusable="false" role="presentation" aria-hidden="true" />
            <RadioCheckedIcon
                focusable="false"
                role="presentation"
                aria-hidden="true"
                className={cn(styles.icon, styles.checkedIcon)}
            />
        </span>
    );
};
