import React from 'react';
import ru from 'date-fns/locale/ru/index.js';
import ReactDatePicker, { ReactDatePickerProps, registerLocale, setDefaultLocale } from 'react-datepicker';

import { renderCustomHeader } from './CustomHeader';
import styles from './DatePicker.module.scss';

registerLocale('ru', ru);
setDefaultLocale('ru');

export const DatePicker = <WithRange extends boolean | undefined = undefined>({
    monthsShown,
    ...rest
}: ReactDatePickerProps<never, WithRange>) => {
    return (
        <ReactDatePicker
            calendarClassName={styles.calendar}
            locale={ru}
            renderCustomHeader={(props) => renderCustomHeader({ monthsShown, ...props })}
            showPopperArrow={false}
            dateFormat="d MMMM, yyyy"
            monthsShown={monthsShown}
            {...rest}
        />
    );
};
