import React from 'react';
import cn from 'classnames';

import styles from './Select.module.scss';
import { SmoothHeightWrapper } from '../SmoothHeightWrapper';

type Option = {
    value: string;
    label: string;
};

export type Props = JSX.IntrinsicElements['select'] & {
    value?: string;
    options: Option[];
    title?: string;
    className?: string;
    containerClassName?: string;
    isError?: boolean;
    isSuccess?: boolean;
    isHighlighted?: boolean;
    onChange: (value: string) => void;
    onFocus?: () => void;
    message?: string;
};

function isValueEmpty(value?: string) {
    return value === undefined || value === '';
}

export const Select: React.FC<Props> = ({
    value,
    title,
    className,
    containerClassName,
    isError,
    isSuccess,
    isHighlighted,
    onChange,
    onFocus,
    required,
    message,
    options,
    placeholder,
}) => {
    const [isOpen, setIsOpen] = React.useState<boolean>(false);
    const [highlightedValue, setHighlitedValue] = React.useState<string>('');

    const rootClassName = cn(
        styles.root,
        {
            [styles.rootStatusHighlighted]: isHighlighted,
            [styles.rootStatusError]: isError,
            [styles.rootStatusSuccess]: isSuccess,
            [styles.rootFilled]: !isValueEmpty(value),
        },
        containerClassName
    );

    const arrowClassName = cn(styles.arrow, isOpen ? styles.arrowUp : styles.arrowDown);

    const handleToggler = () => setIsOpen(!isOpen);

    const handleChange = (selectedValue: string) => {
        setIsOpen(false);
        onChange(selectedValue);
    };

    const getSelectedLabel = () => {
        const selectedOption = options.find((option) => option.value === value);

        return selectedOption?.label || '';
    };

    const handleKeyDown = (e: React.KeyboardEvent) => {
        e.preventDefault();

        switch (e.keyCode) {
            case 13: // Enter
                handleToggler();

                if (highlightedValue) {
                    setHighlitedValue(highlightedValue);
                    handleChange(highlightedValue);
                }

                break;
            case 40: // Down
                if (isOpen) {
                    const prevIndex = options.findIndex((option) => option.value === highlightedValue);
                    const nextIndex = prevIndex + 1 !== options.length ? prevIndex + 1 : 0;
                    setHighlitedValue(options[nextIndex].value);
                } else {
                    handleToggler();

                    if (value) {
                        setHighlitedValue(value);
                    }
                }

                break;
            case 38: // Up
                if (isOpen) {
                    const prevIndex = options.findIndex((option) => option.value === highlightedValue);
                    const nextIndex = prevIndex - 1 >= 0 ? prevIndex - 1 : options.length - 1;
                    setHighlitedValue(options[nextIndex].value);
                } else {
                    handleToggler();

                    if (value) {
                        setHighlitedValue(value);
                    }
                }

                break;
            case 27: // Esc
                setIsOpen(false);
                break;
            default:
                break;
        }
    };

    return (
        <div className={rootClassName}>
            <div className={styles.title}>
                <span>{title}</span>

                {required && <span className={styles.attention}>*</span>}
            </div>

            <div
                className={cn(styles.select, className)}
                role="combobox"
                aria-controls="select-results"
                aria-expanded={isOpen}
                onClick={handleToggler}
                onKeyDown={handleToggler}
                onFocus={onFocus}
                tabIndex={0}
            >
                <input
                    className={styles.input}
                    type="text"
                    placeholder={placeholder}
                    readOnly
                    value={getSelectedLabel()}
                    onKeyDown={handleKeyDown}
                />

                <span className={arrowClassName} />
            </div>

            {isOpen && (
                <ul className={styles.options} role="listbox" id="select-results">
                    {options.map((item) => (
                        <li
                            role="option"
                            aria-selected={value === item.value}
                            className={cn(styles.option, {
                                [styles.optionHighlited]: item.value === highlightedValue,
                            })}
                            key={item.value}
                            onClick={() => handleChange(item.value)}
                            onKeyDown={() => handleChange(item.value)}
                        >
                            {item.label}
                        </li>
                    ))}
                </ul>
            )}

            <SmoothHeightWrapper>
                {Boolean(message) && <div className={styles.message}>{message}</div>}
            </SmoothHeightWrapper>
        </div>
    );
};
