import React from 'react';

export interface RadioGroupProps {
  name: string;
  value: string;
  onChange?: (value: React.ChangeEvent<HTMLInputElement>) => void;
}

export const RadioGroupContext = React.createContext({
  name: '',
  value: '',
    // eslint-disable-next-line @typescript-eslint/no-empty-function,@typescript-eslint/no-unused-vars
  handleChange: (_: React.ChangeEvent<HTMLInputElement>) => {},
});

export const RadioGroup: React.FC<RadioGroupProps> = ({ name, value, onChange, children }) => {
  function handleChange(event: React.ChangeEvent<HTMLInputElement>) {
    if (onChange) {
      onChange(event);
    }
  }

  return <RadioGroupContext.Provider value={{ name, value, handleChange }}>{children}</RadioGroupContext.Provider>;
};
