import React from 'react';

import { Button } from '../Button';
import styles from './WidgetFooter.module.scss';
import { ReactComponent as TimeIcon } from './images/time.svg';

interface Props {
  text: string;
  buttonText: string;
  onButtonClick: () => void;
}

export const WidgetFooter: React.FC<Props> = ({ text, buttonText, onButtonClick }) => {
  return (
    <div className={styles.root}>
      <div className={styles.label}>
        <TimeIcon className={styles.image} />

        <span className={styles.text}>{text}</span>
      </div>

      <Button className={styles.button} variant="outlined" onClick={onButtonClick}>
        {buttonText}
      </Button>
    </div>
  );
};
