import cn from 'classnames';
import React from 'react';
import { ReactDatePickerCustomHeaderProps, ReactDatePickerProps } from 'react-datepicker';

import styles from './CustomHeader.module.scss';
import { ReactComponent as ArrowLeft } from './images/arrowLeft.svg';
import { ReactComponent as ArrowRight } from './images/arrowRight.svg';

const months = [
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
    '',
];

type Props = ReactDatePickerCustomHeaderProps & Pick<ReactDatePickerProps, 'monthsShown'>;

export const renderCustomHeader: React.FC<Props> = ({
    date,
    decreaseMonth,
    increaseMonth,
    prevMonthButtonDisabled,
    nextMonthButtonDisabled,
    monthDate,
    customHeaderCount,
    monthsShown,
}) => {
    const currentMonth = months[monthDate.getMonth()];
    const currentYear = date.getFullYear();
    const monthCaptionText = [currentMonth, currentYear].join(' ');
    const isArrowLeftHidden = monthsShown === 2 && customHeaderCount === 1;
    const isArrowRightHidden = monthsShown === 2 && customHeaderCount === 0;

    return (
        <div className={styles.customHeaderContainer}>
            <button
                onClick={decreaseMonth}
                disabled={prevMonthButtonDisabled}
                className={cn(styles.arrowButton, isArrowLeftHidden && styles.hiddenButton)}
            >
                <ArrowLeft className={styles.arrowIcon} />
            </button>

            <div className={styles.monthCaption}>{monthCaptionText}</div>

            <button
                onClick={increaseMonth}
                disabled={nextMonthButtonDisabled}
                className={cn(styles.arrowButton, isArrowRightHidden && styles.hiddenButton)}
            >
                <ArrowRight className={styles.arrowIcon} />
            </button>
        </div>
    );
};
