import React from 'react';
import { FieldRenderProps } from 'react-final-form';

import { Select } from '../Select';
import { Props as CustomSelectProps } from '../Select/Select';
import { NativeSelect } from '../NativeSelect';
import { Props as NativeSelectProps } from '../NativeSelect/NativeSelect';
import { getFormFieldProps } from '../../helpers';
import styles from './FormSelect.module.scss';

type CommonSelectProps = NativeSelectProps | CustomSelectProps;
export type Props = CommonSelectProps & FieldRenderProps<string, HTMLElement>;

export const FormSelect: React.FC<Props> = ({ input, meta, options, containerClassName, ...rest }) => {
    const props = {
        ...input,
        ...getFormFieldProps(meta, containerClassName),
        options,
        ...rest,
    };

    return (
        <>
            <div className={styles.select}>
                <Select {...props} />
            </div>
            <div className={styles.nativeSelect}>
                <NativeSelect {...props} />
            </div>
        </>
    );
};
